import { CSSProperties } from "react";
import styled, { keyframes } from "styled-components";

import {
	colors, displayFontSizes, Margins, marginsCss, spacingMap,
} from "../../styleguide";

export interface StyledGlobalNavbarItemProps extends Margins {
	accentColor?: string
	accentBackgroundImage?: string
	isActive?: boolean
	tooltipZIndex?: CSSProperties["zIndex"]
}

export const RightBorder = styled.span`
	width: 8px;
	height: 60px;
	position: absolute;
	right: 0;
`;

export const StyledGlobalNavLink = styled.span<StyledGlobalNavbarItemProps>`
	${marginsCss};
	color: ${colors.defaultGray};
	background-color: transparent;
	border: none;

	align-items: center;
	display: flex;

	justify-content: center;

	border-top-left-radius: 12px;
	border-bottom-left-radius: 12px;

	text-align: center;

	height: 60px;
	width: 66px;
	margin-left: 0.375rem;
	padding-right: 0.188rem;
	box-sizing: border-box;

	&:hover {
		color: ${colors.white};
	};

	${({ isActive, accentColor, accentBackgroundImage }) => isActive && ({
		backgroundColor: `rgba(255, 255, 255, 0.1)`,
		"& > span": {
			backgroundColor: accentColor,
			backgroundImage: accentBackgroundImage && `url(${accentBackgroundImage})`,
		},
	})}
`;

const animation = keyframes`
	from {
		opacity: 0.6;
	}

	to {
		opacity: 1;
	}
`;

export const TooltipWrapper = styled.div`
	position: relative;
	display: inline-flex;
	max-width: max-content;
	cursor: pointer;
`;

export const TooltipItem = styled.div<StyledGlobalNavbarItemProps>`
	z-index: ${({ tooltipZIndex }) => tooltipZIndex};
	position: absolute;
	width: 170px;
	padding: 12px ${spacingMap.sm} 12px 22px;
	color: ${colors.black};
	text-align: left;
	background-color: ${colors.white};
	border-radius: 10px;
	box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.25);

	top: 50%;
	right: 0;
	transform: translateX(calc(100% + 5px)) translateY(-50%);

	animation: ${animation} 0.2s linear;
`;

export const TooltipContent = styled.p`
	overflow: hidden;
	font-size: ${displayFontSizes.caption.$fontSize};
	font-family:${displayFontSizes.caption.$fontFamily};
	line-height: ${displayFontSizes.caption.lineHeight};
	text-overflow: ellipsis;
	margin: ${spacingMap.xxs} 0 0 0;
	color: ${colors.darkerGray};
`;

export const LeftBorder = styled.div<StyledGlobalNavbarItemProps>`
	width: 8px;
	height: 100%;
	position: absolute;
	left: 0;
	top: 0;
	border-top-left-radius: 10px;
	border-bottom-left-radius: 10px;
	background-color: ${({ accentColor }) => accentColor};
	background-image: ${({ accentBackgroundImage }) => accentBackgroundImage && `url(${accentBackgroundImage})`};
`;

export const LeftBorderTriangle = styled.div<StyledGlobalNavbarItemProps>`
	position: absolute;
	border-top: 8px solid transparent;
	border-left: 8px solid transparent;
	border-right: 8px solid ${({ accentColor }) => accentColor};
	border-bottom: 8px solid transparent;
	left: -16px;
	top: 50%;
	transform: translateY(-50%);
`;
